import React, { useState } from "react";
import "@chatscope/chat-ui-kit-styles/dist/default/styles.min.css";
import {
  MainContainer,
  ChatContainer,
  MessageList,
  Message,
  MessageInput,
  TypingIndicator,
} from "@chatscope/chat-ui-kit-react";
import EnergyForm from "./EnergyForm"; // ✅ correct casing

const API_KEY = process.env.REACT_APP_OPENROUTER_API_KEY;

function App() {
  const [messages, setMessages] = useState([
    {
      message: "Hello, I'm Energy Chatbot! Ask me anything about energy.",
      sentTime: "just now",
      sender: "Energy Chatbot",
    },
  ]);
  const [isTyping, setIsTyping] = useState(false);

  const handleSend = async (message) => {
    const newMessage = {
      message,
      direction: "outgoing",
      sender: "user",
    };

    const newMessages = [...messages, newMessage];
    setMessages(newMessages);
    setIsTyping(true);
    await sendToOpenRouter(newMessages);
  };

  const sendToOpenRouter = async (chatMessages) => {
    const apiMessages = chatMessages.map((msg) => ({
      role: msg.sender === "Energy Chatbot" ? "assistant" : "user",
      content: msg.message,
    }));

    const payload = {
      model: "openai/gpt-3.5-turbo", // ✅ free and smart
      messages: [
        {
          role: "system",
          content:
            "You are Energy Chatbot, a smart and friendly assistant that answers all energy-related questions for Malaysian users, especially about solar, LEDs, electricity bills, and energy-saving.",
        },
        ...apiMessages,
      ],
    };

    try {
      const res = await fetch("https://openrouter.ai/api/v1/chat/completions", {
        method: "POST",
        headers: {
          Authorization: `Bearer ${API_KEY}`,
          "Content-Type": "application/json",
          "HTTP-Referer": "http://localhost:3000/", // or your Vercel domain
          "X-Title": "EnergyBuddy FYP Chatbot",
        },
        body: JSON.stringify(payload),
      });

      const data = await res.json();

      if (!data.choices || !data.choices[0]) {
        throw new Error("Invalid response from OpenRouter");
      }

      const reply = data.choices[0].message.content;

      setMessages([
        ...chatMessages,
        {
          message: reply,
          sender: "Energy Chatbot",
        },
      ]);
    } catch (err) {
      console.error("❌ Chat error:", err.message);
      setMessages([
        ...chatMessages,
        {
          message: "Sorry, the AI is unavailable right now. Try again later.",
          sender: "Energy Chatbot",
        },
      ]);
    } finally {
      setIsTyping(false);
    }
  };

  return (
    <div className="App" style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
      <EnergyForm />
      <div style={{ position: "relative", height: "800px", width: "700px" }}>
        <MainContainer>
          <ChatContainer>
            <MessageList
              scrollBehavior="smooth"
              typingIndicator={isTyping ? <TypingIndicator content="Energy Chatbot is typing..." /> : null}
            >
              {messages.map((message, i) => (
                <Message key={i} model={message} />
              ))}
            </MessageList>
            <MessageInput placeholder="Ask about energy, bills, solar, or tips..." onSend={handleSend} />
          </ChatContainer>
        </MainContainer>
      </div>
    </div>
  );
}

export default App;
